:root {
  --pri-font:"League Spartan", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --dark-cyan: hsl(180, 29%, 50%);
  --gray-cyan: hsl(180, 52%, 96%);
  --gray-cyanII: hsl(180, 31%, 95%);
  --dark-gray-cyan: hsl(180, 8%, 52%);
  --v-dark-gray-cyan: hsl(180, 14%, 20%);
}

html {
  font-size: 15px;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--pri-font);
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

nav {
  background-color: var(--dark-cyan);
  height: 7.5rem;
  overflow: hidden;
}

body {
  background-color: var(--gray-cyan);
}

.filtersCtn {
  left: 50%;
  transform: translate(-50%, 0%);
  transition: all 0.2s ease-in-out;
}
.filtersCtn button {
  background-color: var(--gray-cyan);
  overflow: hidden;
  transition: all 0.2s ease-in-out;
  color: var(--dark-cyan) !important;
}
.filtersCtn button div {
  background-color: var(--dark-cyan);
  transition: all 0.2s ease-in-out;
}
.filtersCtn button div:hover {
  color: #fff;
  background-color: var(--v-dark-gray-cyan);
}
.filtersCtn .clear {
  transition: all 0.2s ease-in-out;
}
.filtersCtn .clear:hover {
  color: var(--dark-cyan);
  text-decoration: underline;
}

.listingsCtn .card {
  border-left: 5px solid var(--dark-cyan);
}
.listingsCtn .card__infoSection--companyInfo .header .companyName {
  color: var(--dark-cyan);
}
.listingsCtn .card__infoSection--companyInfo .header span {
  color: #fff;
}
.listingsCtn .card__infoSection--companyInfo .header .new {
  background-color: var(--dark-cyan);
}
.listingsCtn .card__infoSection--companyInfo .header .featured {
  background-color: var(--v-dark-gray-cyan);
}
.listingsCtn .card__infoSection--companyInfo .position {
  transition: all 0.2s ease-in-out;
}
.listingsCtn .card__infoSection--companyInfo .position:hover {
  color: var(--dark-cyan);
}
.listingsCtn .card__infoSection .listingInfo {
  color: var(--dark-gray-cyan);
}
.listingsCtn .card__tagsSection {
  color: var(--dark-cyan);
}
.listingsCtn .card__tagsSection button {
  background-color: var(--gray-cyan);
  transition: all 0.2s ease-in-out;
}
.listingsCtn .card__tagsSection button:hover {
  color: #fff;
  background-color: var(--dark-cyan);
}

